"use client";
import { useAuth } from "@/components/AuthContext";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { DeleteIcon, Edit, MoreHorizontal } from "lucide-react";
import { menuService } from "@/lib/services/menuService";
import { DropdownMenu } from "@radix-ui/react-dropdown-menu";
import {
  DropdownMenuContent,
  DropdownMenuLabel,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover";
import { Button } from "@/components/ui/button";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import AddMenu from "@/components/admins/menu/AddMenu";
import { Separator } from "@/components/ui/separator";
import { MenuType } from "@/type/menu.model";

import {
  Pagination,
  PaginationContent,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";
import { usePagination } from "@/hook/usePagination";

export default function Page() {
  const { isAuthenticated } = useAuth();
  const router = useRouter();
  const [menusData, setmenusData] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [actionType, setActionType] = useState("add");
  const [menuDetail, setmenuDetail] = useState<MenuType>();
  const pagination = usePagination();

  useEffect(() => {
    if (!isAuthenticated) {
      router.push("/login");
    }
  }, [isAuthenticated]);

  useEffect(() => {
    getmenus();
  }, [pagination.page, pagination.search]);

  const getmenus = () => {
    menuService
      .getMenus({
        page: pagination.page,
        search: pagination.search,
        page_size: pagination.pageSize,
      })
      .then((res) => {
        setIsLoading(false);
        setmenusData(res.data.data.results);
        pagination.setTotalCount(res.data.data.count);
      })
      .catch((error) => {
        setIsLoading(false);
        console.log(error);
      });
  };

  const handleUpdateButton = (menuDetail: MenuType) => {
    setmenuDetail(menuDetail);

    setActionType("edit");
    setOpen(true);
  };

  const handleDeleteBook = async (id: string) => {
    setIsLoading(true);
    menuService
      .deleteMenu(id)
      .then((res) => {
        setIsLoading(false);
        setmenusData((prev) => {
          return prev.filter((book: any) => book?.id !== id);
        });
      })
      .catch((error) => {
        setIsLoading(false);
        console.log(error);
      });
  };

  useEffect(() => {
    if (!open) {
      setmenuDetail({
        id: "",
        name: "",
        description: "",
        menu_category: "",
        image: "",
      });
      setActionType("add");
    }
  }, [open]);

  return (
    <div className="w-full">
      <div className="flex justify-between items-center px-2 lg:px-12 py-4">
        <input
          className="w-[250px] lg:max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          placeholder="search"
          onChange={(e) => pagination.setSearch(e.target.value)}
        />

        <Dialog open={open} onOpenChange={setOpen}>
          <DialogTrigger className="bg-black hover:bg-gray-500 cursor-pointer text-white px-4 py-2 rounded-lg shadow-lg">
            Add Menu
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Add Menu</DialogTitle>
            </DialogHeader>
            <Separator />
            <AddMenu
              setOpen={setOpen}
              getMenus={getmenus}
              actionType={actionType}
              menuDetail={menuDetail}
            />
          </DialogContent>
        </Dialog>
      </div>
      <div className="flex flex-col justify-center px-2 lg:px-12">
        <Table className="bg-white">
          <TableHeader>
            <TableRow>
              <TableHead>ID</TableHead>
              <TableHead>Name</TableHead>
              <TableHead>Price</TableHead>
              <TableHead>Preparation Time</TableHead>
              <TableHead>Description</TableHead>

              <TableHead>Action</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {!isLoading &&
              menusData.map((menu: any, index) => {
                return (
                  <TableRow key={index}>
                    <TableCell>{menu?.id}</TableCell>
                    <TableCell>{menu?.name}</TableCell>
                    <TableCell>{menu?.price}</TableCell>
                    <TableCell>{menu?.preparation_time}</TableCell>
                    <TableCell>{menu?.description}</TableCell>

                    <TableCell>
                      <DropdownMenu>
                        <DropdownMenuTrigger>
                          <MoreHorizontal className="h-5 w-5 text-gray-400" />
                        </DropdownMenuTrigger>
                        <DropdownMenuContent className="w-18">
                          <DropdownMenuLabel className="flex items-center text-gray-700 cursor-pointer">
                            <Popover>
                              <PopoverTrigger className="flex items-center text-gray-700 cursor-pointer">
                                <DeleteIcon className="mr-4 text-[12px]" />
                                Delete
                              </PopoverTrigger>
                              <PopoverContent className="w-64">
                                are you sure to delete this book?
                                <div className="flex justify-center mt-4">
                                  <Button className="text-red-600 bg-transparent outline-red-600">
                                    No
                                  </Button>
                                  <Button
                                    className="text-green-600 bg-transparent outline-green-600"
                                    onClick={() => handleDeleteBook(menu.id)}
                                  >
                                    Yes
                                  </Button>
                                </div>
                              </PopoverContent>
                            </Popover>
                          </DropdownMenuLabel>
                          <DropdownMenuLabel
                            className="flex item-center text-gray-700 cursor-pointer"
                            onClick={() => handleUpdateButton(menu)}
                          >
                            <Edit className="mr-4 text-[12px]" />
                            Update
                          </DropdownMenuLabel>
                        </DropdownMenuContent>
                      </DropdownMenu>
                    </TableCell>
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
        <Pagination className="self-start">
          <PaginationContent>
            <PaginationItem
              onClick={isLoading ? undefined : pagination.handlePrevPage}
            >
              <PaginationPrevious />
            </PaginationItem>
            {pagination.totalCount > 0 && (
              <>
                {
                  <PaginationItem>
                    <PaginationLink>
                      Total:{pagination.totalCount}
                    </PaginationLink>
                  </PaginationItem>
                }
              </>
            )}

            <PaginationItem
              onClick={isLoading ? undefined : pagination.handleNextPage}
              className={
                menusData.length === 0 ? "pointer-events-none opacity-50 " : ""
              }
            >
              <PaginationNext />
            </PaginationItem>
          </PaginationContent>
        </Pagination>
      </div>
    </div>
  );
}
